& {
  @import "../variables/bootstrap.less";

  /** DCWindow */

  .DCWindow-modal {
    opacity: .5;
  }

  .DCWindow-dialog .modal-body .icon-bubble,
  .DCWindow-notice .modal-body .icon-bubble {
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
  }

  .DCWindow-dialog .modal-body .icon-error,
  .DCWindow-notice .modal-body .icon-error {
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
  }

  .DCWindow-dialog .modal-body .icon-info,
  .DCWindow-notice .modal-body .icon-info {
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
  }

  .DCWindow-dialog .modal-body .icon-question,
  .DCWindow-notice .modal-body .icon-question {
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
  }

  .DCWindow-dialog .modal-body .icon-warn,
  .DCWindow-notice .modal-body .icon-warn {
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
  }

  .DCWindow-dialog .modal-body .icon-success,
  .DCWindow-notice .modal-body .icon-success {
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
  }

  .DCWindow {
  }

  .DCWindow.yui3-panel .modal-header .yui3-widget-buttons {
    top: 15px;
    right: 15px;
  }

  .DCWindow .modal-title {
    display: inline-block;
    font-weight: bold;
  }

  .DCWindow .modal-title-icon.glyphicon {
    margin-right: 10px;
  }

  .DCWindow .modal-body {
    overflow-y: auto;
  }
  // [MOJ-3334]: make visible scrollbars on mac for web-kit
  // @author: http://simurai.com/blog/2011/07/26/webkit-scrollbar/
  // @see: http://stackoverflow.com/questions/18317634/force-visible-scrollbar-in-firefox-on-mac-os-x
  .DCWindow .modal-body::-webkit-scrollbar {
    -webkit-appearance: none;
    width: 7px;
  }

  .DCWindow .modal-body::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(0, 0, 0, .5);
    -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, .5);
  }

  .DCWindow .modal-footer {
    margin-top: 0;
  }

  /* prevent other css */
  .DCWindow .btn-default {
    text-shadow: none;
    box-shadow: none;
  }

  .DCWindow .modal-header .btn-default:hover,
  .DCWindow .modal-header .btn-default:focus,
  .DCWindow .modal-header .btn-default:active,
  .DCWindow .modal-header .btn-default.active {
    background-color: transparent;
  }

  .DCWindow-maximized .modal-content {
    border-radius: 0;
    box-shadow: none;
  }

  .DCWindow-dialog,
  .DCWindow-notice {
  }

  .DCWindow-dialog .modal-body,
  .DCWindow-notice .modal-body {
    max-height: 240px;
  }

  .DCWindow-dialog.DCWindow-maximized .modal-body,
  .DCWindow-notice.DCWindow-maximized .modal-body {
    max-height: none;
  }

  .DCWindow-TransferWizard {
  }

  .DCWindow-SelectInsurancePayer {
  }

  .DCWindow-SelectPhysician {
  }

  .DCWindow-SelectDevice {
  }

  .DCWindow-SelectPatient {
  }

  .DCWindow-MergePatient {
  }

  .DCWindow-MergePatient div h4 {
    margin-top: 20px;
  }
  .DCWindow-MergePatient .row-conflict .bg-conflict {
    color: #a94442;
    background-color: #f2dede;
    border-color: #ebccd1;
  }
  .DCWindow-MergePatient .row-ok .bg-conflict {
    color: #3c763d;
    background-color: #dff0d8;
    border-color: #d6e9c6;
  }
  .DCWindow-MergePatient .btn-xs .glyphicon,
  .DCWindow-MergePatient .btn-group-xs .glyphicon {
    vertical-align: text-top;
  }
  .DCWindow-MergePatient th[colspan] {
    border-top: 0 none;
  }
  .DCWindow-MergePatient th h4 {
    margin-top: 0;
  }
  .DCWindow-MergePatient tr.foot td {
    border-bottom: 0 none;
  }
  .DCWindow-MergePatient .btn-group-justified > .btn, .DCWindow-MergePatient .btn-group-justified > .btn-group {
    width: 50%;
  }

  .DCWindow-DiffPatient {
  }

  .DCWindow-DiffPatient th h4 {
    margin-top: 20px;
  }
  .showAuditDiffDialog table tbody td.bg-oldValue,
  .DCWindow-DiffPatient .row-conflict .bg-conflict {
    color: #a94442;
    background-color: #f2dede;
    border-color: #ebccd1;
  }
  .showAuditDiffDialog table tbody td.bg-newValue,
  .DCWindow-DiffPatient .row-conflict .bg-noconflict {
    color: #3c763d;
    background-color: #dff0d8;
    border-color: #d6e9c6;
  }
  .DCWindow-DiffPatient .btn-xs .glyphicon,
  .DCWindow-DiffPatient .btn-group-xs .glyphicon {
    vertical-align: text-top;
  }
  .DCWindow-DiffPatient th[colspan] {
    border-top: 0 none;
  }
  .DCWindow-DiffPatient tr:not(.row-ok):not(.row-conflict) th:not([colspan]) {
    background-color: #f8f8f8;
  }
  .DCWindow-DiffPatient tbody tr:first-child h4 {
    margin-top: 0;
  }
  .DCWindow-DiffPatient tr.foot td {
    border-bottom: 0 none;
  }
  .DCWindow-DiffPatient .btn-group-justified > .btn, .DCWindow-DiffPatient .btn-group-justified > .btn-group {
    width: 50%;
  }

  .DCWindow-DataPatientFromCard {
  }

  .DCWindow-DataPatientFromCard th h4 {
    margin-top: 20px;
  }
  .DCWindow-DataPatientFromCard tr.cardreader_datapatientFromCard-headline th {
    border-top: 0 none;
  }
  .DCWindow-DataPatientFromCard tr.cardreader_datapatientFromCard-header th {
    background-color: #f8f8f8;
  }
  .DCWindow-DataPatientFromCard tbody tr:first-child h4 {
    margin-top: 0;
  }

}